@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  position: relative;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  /* font-family: 'Open Sans', sans-serif; */
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.nav-container {
  background-color: rgb(245, 223, 223, 0.9);
  position: fixed;
  top: 0;
  height: 8%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  padding-top: auto;
  padding-left: 2%;
  padding-right: 7%;
  z-index: 1;
}

.nav-item a {
  margin-left: 15%;
  text-decoration: none;
  font-size: 1.1em;
  color: rgb(0, 0, 0);
  font-weight: bolder;
}

.nav-container a:hover {
  color: rgb(142, 204, 207);
  border-bottom: 2px rgb(142, 204, 207) solid;
}

.nav-item a:focus {
  color: rgb(142, 204, 207);
}

.name-logo {
  text-decoration: none;
  font-weight: bolder;
  font-size: 1.1em;
  color: black;
}

.name-logo:hover {
  color: rgb(142, 204, 207);
  text-decoration: none;
}

@media screen and (min-width: 1505px) and (max-width: 1900px) {
  .nav-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    padding-left: 2%;
  }
  .nav-item a {
    font-size: 1.3rem;
    margin-left: 0%;
    margin-right: 18%;
  }

  .name-logo {
    text-decoration: none;
    color: black;
    font-weight: bolder;
    font-size: 1.3rem;
  }
}

