.experience,
.projects-container,
.about-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.about-container div,
.experience div,
.projects-container div {
  width: 100%;
  margin: 5% 0% 3.5% 7%;
}

.about-container h4,
.projects-container h4,
.experience h4 {
  font-weight: bold;
}

.paragraph p {
  margin-left: -45%;
  font-size: 1.2rem;
  font-weight: normal;
  padding-right: 20%;
}

.experience a {
  text-decoration: none;
  color: rgb(142, 204, 207);
}

.experience a:hover {
  text-decoration: none;
  color: rgb(142, 204, 207);
  border-bottom: 2px rgb(142, 204, 207) solid;
  font-weight: bold;
}

.projects a {
  text-decoration: none;
  color: rgb(70, 165, 170);
}

.projects a:hover {
  text-decoration: none;
  color: rgb(142, 204, 207);
