.card-container {
  margin-top: 6%;
}

.card-container .card {
  width: 100%;
  margin-top: 20%;
  padding-top: 2%;
  margin: 0 auto;
  border: none;
  background-color: rgb(245, 223, 223, 0.9);
  margin-bottom: 5%;
}

.card div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card a {
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
  text-decoration: none;
  color: black;
  padding-bottom: 0;
  margin-bottom: 1%;
  text-decoration: underline;
}

.card a:hover {
  text-decoration: underline;
  color: rgb(142, 204, 207);
}

.project-img {
  border: white 3px solid;
  margin-bottom: 2%;
}

.project-title {
  margin-bottom: 2%;
}

.project-description li {
  list-style: none;
  text-align: center;
